<template>
  <div class="box">
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="#1989fa">
      <van-swipe-item
        v-for="item of carousel"
        :key="item.id"
        @click="$bus.$emit('details', item.content)"
      >
        <img :src="item.img_url"
      /></van-swipe-item>
    </van-swipe>
    <!-- 热门书籍 -->
    <div class="hot">
      <Sections>
        <img src="@/assets/鲸云小说/10003.png" slot="imgone" />
        <router-link to="/hotmore" slot="more">更多</router-link>
      </Sections>
      <Recommendone :arr="hot_top"></Recommendone>
    </div>
    <!-- 新书推荐 -->
    <div class="new">
      <Sections>
        <img src="@/assets/鲸云小说/10004.png" slot="imgone" />
        <router-link to="/newmore" slot="more">更多</router-link>
      </Sections>
      <Recommendone :arr="new_top"></Recommendone>
    </div>
    <!-- 好书推荐 -->
    <div class="friend">
      <Sections>
        <img src="@/assets/鲸云小说/10005.png" slot="imgone" />
        <img src="@/assets/鲸云小说/10006.png" slot="imgtwo" />
      </Sections>
      <Recommendtwo :arr="good_recommend"></Recommendtwo>
    </div>
    <!-- 最新更新 -->
    <div class="update">
      <Sections>
        <img src="@/assets/鲸云小说/10007.png" slot="imgone" />
        <router-link to="/updatemore" slot="more">更多</router-link>
      </Sections>
      <Recommendone :arr="update_top"></Recommendone>
    </div>
    <!-- vip热门 -->
    <div class="vip">
      <Sections>
        <img src="@/assets/鲸云小说/10008.png" slot="imgone" />
        <router-link to="/vipmore" slot="more">更多</router-link>
      </Sections>
      <Recommendone :arr="vip_data"></Recommendone>
    </div>
    <!-- 主力推荐 -->
    <div class="themain">
      <Sections>
        <img src="@/assets/鲸云小说/10010.png" slot="imgone" />
        <img src="@/assets/鲸云小说/10011.png" slot="imgtwo" />
      </Sections>
      <Recommendthree :arr="main_recommend"></Recommendthree>
    </div>
    <div class="footer"> 
      <Sections>
        <img src="@/assets/鲸云小说/10012.png" slot="imgone" />
        <img src="@/assets/鲸云小说/10013.png" slot="imgtwo" />
      </Sections>
      <Recommendfour :arr="home_recommend"></Recommendfour>
    </div>
  </div>
</template>
  
<script>
import Sections from "@/components/Section.vue";
import Recommendone from "@/components/Recommendone.vue";
import Recommendtwo from "@/components/Recommendtwo.vue";
import Recommendthree from "@/components/Recommendthree.vue";
import Recommendfour from "@/components/Recommendfour.vue";
export default {
  name: "Mymain",
  components: { Sections, Recommendone,Recommendtwo,Recommendthree,Recommendfour},
  data() {
    return {
      carousel: [],
      hot_top: [],
      new_top: [],
      good_recommend: [],
      update_top:[],
      vip_data:[],
      main_recommend:[],
      home_recommend:[],
    };
  },
  activated(){
    this.$bus.$emit('active', 0)
  },
  mounted() {

    this.axios
      .get("https://jyapi.jyacg.com/api/common/bannerList")
      .then((res) => {
        if (res.status == 200) {
          this.carousel = res.data.data.splice(0, 4);
        }
      });
    this.axios.get("https://jyapi.jyacg.com/web/index").then((res) => {
      if (res.status == 200) {
        this.hot_top = res.data.data.hot_top;
        this.new_top = res.data.data.new_top;
        this.good_recommend = res.data.data.good_recommend
      }
    });
    this.axios.get("https://jyapi.jyacg.com/web/index_sec").then((res) => {
      if (res.status == 200) {
        this.update_top = res.data.data.update_top
       this.vip_data = res.data.data.vip_data
       this.main_recommend = res.data.data.main_recommend
       
      }
    });
    this.axios.get("https://jyapi.jyacg.com/web/index_third").then((res) => {
      if (res.status == 200) {
        this.home_recommend =res.data.data.home_recommend
      }
    });
  },
  
};
</script>
  <style lang="scss" scoped>
.box {
  width: 100%;
  background-color: #f4f3f1;
}
.box > div {
  box-shadow: 0px 0px 5px rgba(0, 0, 0, .1) ; 
  background-color: #fff;
}
.new,.update,.friend,.vip,.update,.themain,.footer{
  margin-top: 20px;
}
.my-swipe {
  margin-top: 16px;

  img {
    border-radius: 10px;
    width: 100%;
    height: 125px;
  }
}
</style>